:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  list-style-type: none;
}

.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid #0000001f;
  border-radius: 4px;
  flex-direction: column;
  margin: 15px auto;
  padding: 12px;
  display: flex;
}

.feedback-form h1 {
  text-align: center;
  font-size: x-large;
}

.feedback-form input {
  width: 100%;
  border-radius: 4px;
  padding: 8px;
}

.select-container {
  justify-content: space-around;
  margin: 15px;
  display: flex;
}

.select-container label {
  font-size: large;
}

#quantity {
  min-width: 50px;
  border-radius: 4px;
  padding: 5px;
  font-size: large;
}

#combinations {
  max-width: 100px;
  text-align: right;
  border-radius: 4px;
  padding: 5px;
  font-size: large;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  border-radius: 4px;
  padding: 8px 12px;
}

.title-history {
  margin: 15px auto;
}

.textarea-container {
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin-bottom: 16px;
  display: flex;
}

textarea {
  font: inherit;
  font-family: inherit;
  line-height: inherit;
  resize: none;
  width: 100%;
  border-radius: 4px;
  padding: 5px;
  overflow: hidden;
}

textarea + .line-numbers {
  counter-reset: line-number;
  margin-left: -20px;
  padding: 5px;
}

textarea + .line-numbers .line-number:before {
  content: counter(line-number);
  counter-increment: line-number;
  width: 2em;
  text-align: right;
  color: #999;
  margin-right: .5em;
  display: inline-block;
}

